  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  min-height: 200px;
}

.Section-title{
  font-family: 'Roboto Condensed', sans-serif;
  background-color: rgba(10, 161, 199, 0.24);
  width: 100%;
  height: 60px;
  padding-top: 10px;
}


* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.Section-content {
  padding: 15px;
